.container, .col {
    font-size: 1.1em;
}

.rowArea {
    height: 100%;
}

.center {
    text-align: center;
}

.btn-toolbar {
    justify-content: space-evenly;
}

.btn, .btn:hover, .btn:active, .btn:focus {
    background-color: rgb(170,0,0);
    border-color: black;
}

.btn:hover {
    box-shadow: 2px 2px 2px black;
}

ol {
    list-style: none;
}

.jumbotron {
    text-align: center;
    border-radius: 0;
    background-color: rgb(170,0,0);
    color: white;
}

.element {
    font-weight: bold;
    color: rgb(170,0,0);
}

.navbar-nav {
    justify-content: space-evenly;
    width: 75%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 5%;
    border: 1px solid black;
    border-radius: 5px;
    flex-wrap: wrap;
    background-color: #e9ecef;
}

.projectItem {
    text-align: center;
    border: 1px solid gray;
    margin: 1%;
    min-height: 150px;
}

#projectArea {
    margin: 1%;
}

.container {
    margin-bottom: 10%;
    margin-top: 10%;
}

.nav-link {
    color: rgb(170,0,0) !important;
    font-weight: bold;
    cursor: pointer;
}

.navbar-light:hover .navbar-nav:hover .nav-link:hover {
    color: black !important;
}

.hide {
    display: none;
}

.card-deck {
    justify-content: space-evenly;
}

.card-footer, .card-header {
    text-align: center;
}

.full {
    max-width: 100% !important;
}

.card {
    font-size: 0.8em;
    max-width: 100%;
    border: solid 1px rgb(170,0,0);
}

.card-header {
    font-weight: bold;
}

.card-body {
    border-bottom: 1px solid rgb(170, 0, 0);
    border-top: 1px solid rgb(170, 0, 0);
}

.card-text, .card-body {
    word-wrap: break-word;
}

.card-link + .card-link {
    margin-left: 0;
}

canvas {
    margin-left: auto;
    margin-right: auto;
    cursor: pointer;
    width: 50%;
    border: 1px solid black;
    border-radius: 10px;
}

.projectButton {
    border: 0;
    border-radius: 0;
    padding: 0;
    background: none !important;
    color: #007bff;
    font-size: 1em;
    cursor: pointer;
}

.projectButton:hover {
    color: #3366BB;
    border: none;
    outline: none;
}

.pagination {
    justify-content: center;
}

.fa-download, .fa-expand-arrows-alt {
    color: white !important;
}

.fa-angle-down, .fa-angle-up {
    color: white !important;
}

.fab, .far, .fas {
    color: rgb(170,0,0);
}

@media screen and (max-width: 500px) {
    .viewPDF {
        display: none;
    }
}

@media (max-width: 800px) {
    .projectButton {
        display: none;
    }
}

@media (min-width: 576px) {
    .card-deck .card {
        flex: 4 0 auto;
        margin-bottom: 15px;
    }

    .card {
        max-width: 40%;
    }
}